"use client";

import type { Metadata } from "next";
import { usePathname } from "next/navigation";
import Link from "next/link";
import "./globals.css";
import { FaHome, FaChartLine, FaDollarSign, FaUniversity, FaUser, FaSignInAlt } from "react-icons/fa";

export const metadata: Metadata = {
  title: "Create Next App",
  description: "Generated by create next app",
};

export default function RootLayout({ children }: { children: React.ReactNode }) {
  const pathname = usePathname();

  // กำหนด active tab จาก path ปัจจุบัน
  let activeTab = "";
  if (pathname.startsWith("/dashboard")) {
    activeTab = "home";
  } else if (pathname.startsWith("/chart")) {
    activeTab = "chart";
  } else if (pathname.startsWith("/asset")) {
    activeTab = "asset";
  } else if (pathname.startsWith("/finance")) {
    activeTab = "finance";
  }

  // สมมุติว่า isLoggedIn จัดการผ่าน context หรือ state ภายนอก (ตัวอย่างนี้ใช้ false)
  const isLoggedIn = false;

  return (
    <html lang="en">
      <body className="flex h-screen bg-gray-900 text-white">
        {/* Sidebar */}
        <aside className="w-64 bg-gray-800 p-6 flex flex-col">
          {/* Logo */}
          <div className="flex items-center space-x-2 mb-8">
            <div className="bg-green-500 p-2 rounded-lg">
              <span className="text-black font-bold text-xl">G</span>
            </div>
            <h1 className="text-2xl font-bold">EQUITY</h1>
          </div>

          {/* Menu */}
          <nav className="flex flex-col space-y-4">
            <Link
              href="/dashboard"
              className={`flex items-center space-x-2 p-3 rounded-lg ${
                activeTab === "home" ? "bg-gray-700" : "hover:bg-gray-700"
              }`}
            >
              <FaHome />
              <span>Home</span>
            </Link>
            <Link
              href="/chart"
              className={`flex items-center space-x-2 p-3 rounded-lg ${
                activeTab === "chart" ? "bg-gray-700" : "hover:bg-gray-700"
              }`}
            >
              <FaChartLine />
              <span>Chart</span>
            </Link>
            <Link
              href="/asset"
              className={`flex items-center space-x-2 p-3 rounded-lg ${
                activeTab === "asset" ? "bg-gray-700" : "hover:bg-gray-700"
              }`}
            >
              <FaDollarSign />
              <span>Asset</span>
            </Link>
            <Link
              href="/finance"
              className={`flex items-center space-x-2 p-3 rounded-lg ${
                activeTab === "finance" ? "bg-gray-700" : "hover:bg-gray-700"
              }`}
            >
              <FaUniversity />
              <span>Finance</span>
            </Link>
          </nav>

          {/* Login Button or User Profile */}
          <div className="mt-auto">
            {isLoggedIn ? (
              <div className="flex items-center space-x-2 p-3 rounded-lg bg-gray-700">
                <FaUser />
                <span>Arin</span>
              </div>
            ) : (
              <Link href="/login">
                <button className="w-full flex items-center justify-center space-x-2 p-3 rounded-lg bg-blue-600 text-white font-semibold shadow-lg transition hover:bg-blue-700">
                  <FaSignInAlt />
                  <span>Login</span>
                </button>
              </Link>
            )}
          </div>
        </aside>

        {/* Main Content */}
        <main className="flex-1 p-8 bg-gray-900">{children}</main>
      </body>
    </html>
  );
}
